<template>
	<div class="add-call">
		<!-- <mt-header title="" class="createName">
			<router-link to="/focusFamily" slot="left">
				<mt-button icon="back"></mt-button>
			</router-link>
		</mt-header> -->
		<div>
			<mt-field label="" placeholder="" v-model="family.appellation" :attr="{ maxlength: 8 }" required></mt-field>
			<mt-field label="" placeholder="" v-model="family.tel" :attr="{ maxlength: 11 }" required></mt-field>
		</div>

		<div class="next">
			<mt-button class="next-btn" @click.native="submitForm"></mt-button>
		</div>
	</div>
</template>
<script>
	export default {
		data (){
			return {
				family: {
					appellation: '',
					tel: ''
				}
			}
		},
		methods:{
			submitForm () {
				if (this.family.appellation==''){
					this.$toast({
						message: '',
						position: 'bottom'
					});
					return false
				}
				if(this.family.tel=='') {
					this.$toast({
						message: '',
						position: 'bottom'
					});
					return false

				}
				//
				var resource = this.$resource(this.$store.state.serverIP+'/user_focus/add_focu?'+location.search.substring(1).split("&")[0]);
				resource.save(this.family).then((response) => {
        			this.$toast({
        				message: '',
        				position: 'bottom'
        			});
        			setTimeout(() => {
        				this.$router.go(-1);
        			}, 1000);
				}, (response) => {
     				this.$toast(response.data.error);
				})
			}
		}
	}
</script>
<style scoped>
.add-call{
	padding: 30px 20px;
	font-family: '';
}
</style>



